.register {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.register_navbar {
  width: 100%;
  height: 70px;
  background-color: #7a8e52;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.register_logo a {
  color: aliceblue;
  text-decoration: none;
  font-size: 1.6vw;
  padding: 2vw;
  margin-left: 100px;
}
.register_login_register a {
  color: aliceblue;
  text-decoration: none;
  font-size: 1.6vw;
  padding: 2vw;
  margin-right: 70px;
}

.register_text {
  margin-top: 50px;
  margin-left: 43vw;
  flex: 1;
  font-size: 250%;
  color: #7a8e52;
  font-weight: 900;
}

.register_form {
  margin-left: 43%;
  flex: 4;
  width: 50%;
  display: flex;
  flex-direction: column;
}

.register_form div {
  display: flex;
}

.register_form div input {
  margin-top: 10px;
  margin-left: 10px;
  height: 40px;
  width: 60%;
}

.register_form_submit button {
  margin-top: 10px;
  margin-left: 10px;
  background-color: #7a8e52;
  color: aliceblue;
  font-weight: 1000;
  font-size: 25px;
  height: 45px;
  width: 60%;
  border: aliceblue;
}

.already_registered {
  flex: 1;
  margin-left: 44%;
}

@media only screen and (max-width: 450px) {
  .register {
    width: 100vw;
    display: flex;
    flex-direction: column;
  }

  .register_navbar {
    width: 100%;
    height: 70px;
    background-color: #7a8e52;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .register_logo a {
    color: aliceblue;
    text-decoration: none;
    font-size: 22px;
    padding: 2px;
    margin-left: 25px;
  }
  .register_login_register a {
    color: aliceblue;
    text-decoration: none;
    font-size: 22px;
    padding: 2px;
    margin-right: 25px;
  }

  .register_text {
    margin: 25px auto;
    flex: 1;
    font-size: 40px;
    color: #7a8e52;
    font-weight: 900;
  }

  .register_form {
    margin: 20px auto;
    flex: 4;
    width: 80%;
    display: flex;
    flex-direction: column;
  }

  .register_form div {
    display: flex;
    flex-direction: column;
  }

  .register_form div input {
    margin: 10px auto;
    height: 40px;
    width: 100%;
  }

  .register_form_submit button {
    margin: 10px auto;
    background-color: #7a8e52;
    color: aliceblue;
    font-weight: bold;
    font-size: 25px;
    height: 45px;
    width: 100%;
    border: aliceblue;
  }

  .already_registered {
    flex: 1;
    margin: 20px auto;
  }
}
