.login {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login_navbar {
  flex: 1.5;
  width: 100%;
  height: 104px;
  background-color: #7a8e52;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.login_logo a {
  color: aliceblue;
  text-decoration: none;
  font-size: 1.6vw;
  padding: 2vw;
  margin-left: 100px;
}
.login_login_register a {
  color: aliceblue;
  text-decoration: none;
  font-size: 1.6vw;
  padding: 2vw;
  margin-right: 70px;
}

.login h2 {
  margin: 50px;
  flex: 1;
  font-size: 250%;
  color: #7a8e52;
  font-weight: 900;
}
.login_form {
  flex: 4;
  width: 50%;
  display: flex;
  flex-direction: column;
}
.login_form input {
  margin: 1% auto;
  height: 40px;
  width: 60%;
}

.login_submit {
  margin: 1% auto;
  background-color: #7a8e52;
  color: aliceblue;
  font-weight: 1000;
  font-size: 25px;
  height: 45px;
  width: 60%;
  border: aliceblue;
}

.Not_registered {
  flex: 1;
}

@media only screen and (max-width: 450px) {
  .login {
    width: 100vw;
    display: flex;
    flex-direction: column;
  }

  .login_navbar {
    width: 100%;
    height: 70px;
    background-color: #7a8e52;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .login_logo a {
    color: aliceblue;
    text-decoration: none;
    font-size: 22px;
    padding: 2px;
    margin-left: 25px;
  }

  .login_login_register a {
    color: aliceblue;
    text-decoration: none;
    font-size: 22px;
    padding: 2px;
    margin-right: 25px;
  }

  .login h2 {
    margin: 50px;
    font-size: 250%;
    color: #7a8e52;
    font-weight: bold;
  }
  .login_form {
    flex: 10;
    width: 80%;
    display: flex;
    flex-direction: column;
  }
  .login_form input {
    margin: 10px auto;
    height: 40px;
    width: 100%;
  }

  .login_submit {
    margin: 10px auto;
    background-color: #7a8e52;
    color: aliceblue;
    font-weight: 1000;
    font-size: 25px;
    height: 45px;
    width: 100%;
  }
}
