.landing_container {
  margin: 0px;
}
.landing_navbar {
  width: 100%;
  height: 104px;
  background-color: #7a8e52;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.landing_logo a {
  color: aliceblue;
  text-decoration: none;
  font-size: 1.6vw;
  padding: 2vw;
  margin-left: 100px;
}

.landing_login_register a {
  color: aliceblue;
  text-decoration: none;
  font-size: 1.6vw;
  padding: 2vw;
  margin-right: 70px;
}
.landing_body {
  margin-top: 5%;
  margin-left: 100px;
  padding: 2vw;
  display: flex;
}
.landing_body div h2 {
  margin: 0px;
  font-size: 2.626vw;
  color: #7a8e52;
}

.landing_body div p {
  margin-top: 13%;
  font-size: 1vw;
  width: 80%;
  opacity: 0.75;
  color: #7a8e52;
}
.landing_body div {
  flex: 1;
}
.landing_body div img {
  margin-left: 50%;
  width: 40%;
}

@media only screen and (max-width: 450px) {
  .landing_navbar {
    width: 100%;
    height: 70px;
    background-color: #7a8e52;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .landing_logo a {
    color: aliceblue;
    text-decoration: none;
    font-size: 22px;
    padding: 5px;
    margin-left: 25px;
  }

  .landing_login_register a {
    color: aliceblue;
    text-decoration: none;
    font-size: 22px;
    padding: 2px;
    margin-right: 25px;
  }
  .landing_body {
    width: 90%;
    margin: 20px auto;
    padding: 2px;
    display: flex;
    flex-direction: column;
  }
  .landing_body div h2 {
    margin: 0px;
    font-size: 40px;
    color: #7a8e52;
  }

  .landing_body div p {
    margin-top: 20px;
    font-size: 20px;
    width: 80%;
    opacity: 0.75;
    color: #7a8e52;
  }

  .landing_body div img {
    margin: 20px auto;
    width: 80%;
  }
}
