.mypost {
  margin: 100px auto;
  width: 60vw;
  display: flex;
}

.mypost_image {
  flex: 1;
  margin-left: 20px;
}

.mypost_image img {
  width: 100%;
  height: 100%;
}
.mypost_body {
  margin-left: 30px;
  flex: 2;
}
.mypost_blank {
  width: 20px;
  background-color: #7a8e52;
}
.mypost_body_upper_header {
  display: flex;
  justify-content: space-between;
}
.mypost_body_upper_header_date {
  font-size: 12px;
  opacity: 0.75;
}

.mypost_body_upper_header_edit_delete a {
  margin-right: 20px;
}

.mypost_body_upper_header_edit_delete button {
  border: none;
  padding: 8px;
}

.mypost_body_heading {
  font-size: 30px;
  font-weight: bold;
  color: #7a8e52;
}
.mypost_body_heading a {
  text-decoration: none;
  color: #7a8e52;
}

.mypost_body_main_content {
  font-size: 15px;
  font-weight: lighter;
  opacity: 0.8;
}

.mypost_body_main_content div {
  margin-left: 85%;
}

/* Media Query for 450 px and lower screens */

@media (max-width: 450px) {
  .mypost {
    margin: 50px auto;
    width: 90vw;
    display: flex;
    flex-direction: column;
  }

  .mypost_image {
    width: 100%;
    margin: 10px auto;
  }

  .mypost_image img {
    width: 100%;
    height: 100%;
  }
  .mypost_body {
    width: 100%;
    margin: 10px auto;
  }
  .mypost_blank {
    width: 0px;
    background-color: #7a8e52;
  }
  .mypost_body_upper_header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 5px auto;
  }
  .mypost_body_upper_header_date {
    font-size: 10px;
    opacity: 0.75;
  }

  .mypost_body_upper_header_edit_delete button {
    border: none;
    padding: 2px;
    color: #eeeeee;
    background-color: #7a8e52;
  }

  .mypost_body_heading {
    font-size: 25px;
    font-weight: bold;
    color: #7a8e52;
  }

  .mypost_body_heading a {
    text-decoration: none;
    color: #7a8e52;
  }

  .mypost_body_main_content {
    font-size: 15px;
    font-weight: lighter;
    opacity: 0.8;
  }

  .mypost_body_main_content div {
    margin-left: 70%;
  }
}
