.home {
  margin: 0px;
  display: flex;
}

.home_content {
  margin-top: 50px;
  margin-left: 155px;
  display: flex;
  flex-direction: column;

  color: #7a8e52;
  width: 30%;
}
.home_content h1 {
  font-size: 60px;
}

.home_empty {
  margin-left: 33%;
  margin-top: 20%;
  width: 200px;
  height: 330px;
  background-color: #7a8e52;
}

@media only screen and (max-width: 450px) {
  .home {
    margin: 0px;
    display: flex;
    flex-direction: column;
  }

  .home_content {
    width: 90vw;
    margin: 25px auto;
    display: flex;
    flex-direction: column;
    color: #7a8e52;
  }
  .home_content h1 {
    font-size: 40px;
  }

  .home_empty {
    margin-top: 20px;
    width: 100%;
    height: 100px;
    background-color: #7a8e52;
  }
}
