.navbar {
  margin: 0px;
  height: 12vh;
  display: flex;
  justify-content: space-between;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 5px 0 rgba(0, 0, 0, 0.12);
  background: #eee;
  font-size: 20px;
  align-items: center;
}

.navbar a {
  color: #7a8e52;
  font-weight: 600;
}

a {
  text-decoration: none;
}

.navbar_left {
  width: 30%;
  display: flex;
  margin-left: 10%;
  align-items: center;
}

.navbar_home {
  flex: 1;
}
.navbar_menu_icon {
  display: none;
}
.navbar_menu_icon img {
  width: 30px;
}

.navbar_create_post {
  flex: 1;
}
.navbar_posts {
  flex: 1;
  display: flex;
  flex-direction: column;
  position: relative;
}

/* .navbar_post {
  margin-left: 40px;
} */

.navbar_mypost {
  position: absolute;
  left: -45%;
  top: 25px;
  width: 200px;
  background-color: #7a8e52;
  text-align: center;
}
.navbar_mypost a {
  padding: 10px;
  color: #eee;
}

.navbar_allpost {
  position: absolute;
  left: -45%;
  top: 65px;
  width: 200px;
  background-color: #7a8e52;
  text-align: center;
}

.navbar_allpost a {
  padding: 10px;
  color: #eee;
}
.navbar_importantpost {
  position: absolute;
  left: -45%;
  top: 105px;
  width: 200px;
  background-color: #7a8e52;
  text-align: center;
}
.navbar_importantpost a {
  padding: 10px;
  color: #eee;
}
.navbar_ul_right {
  display: flex;
  margin-right: 10%;
}
.navbar_username {
  text-align: center;
  width: 10vw;
  padding: 30px 0;
}
.navbar_logout {
  text-align: center;
  width: 10vw;
  padding: 30px 0;
}

@media only screen and (max-width: 450px) {
  .navbar {
    margin: 0px;
    width: 100%;
    height: 70px;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 5px 0 rgba(0, 0, 0, 0.12);
    background: #eee;
    align-items: center;
  }
  .navbar_left {
    display: none;
  }
  .navbar_ul_right {
    display: none;
  }
  .navbar_menu_icon {
    display: block;
    margin-left: 25px;
  }
  .sidebar {
    height: 65vh;
    width: 200px;
    background-color: #7a8e52;
    position: fixed;
    z-index: 1;
    transition: 0.5s;
    top: 0;
    left: 0;
  }
