.allpost {
  margin: 60px auto;
  width: 60%;
  display: flex;
}

.allpost_empty {
  width: 20px;
  background-color: #7a8e52;
}
.allpost_image {
  flex: 1;
  margin-left: 20px;
}

.allpost_image img {
  width: 100%;
  height: 100%;
}
.allpost_content {
  flex: 1.8;
  display: flex;
  flex-direction: column;
  margin-left: 30px;
}
.allpost_content_author_section {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  opacity: 0.8;
}

.allpost_content_author_section button {
  border: none;
  font-weight: 600;
  padding: 5px;
}

.allpost_content_heading {
  font-size: 26px;
  font-weight: 900;
}
.allpost_content_body p {
  font-size: 16px;
  opacity: 0.8;
}

.allpost_content_body a {
  margin-left: 84%;
  opacity: 0.8;
}

@media only screen and (max-width: 450px) {
  .allpost {
    width: 90vw;
    margin: 40px auto;
    display: flex;
    flex-direction: column;
  }

  .allpost_empty {
    width: 0px;
    background-color: #7a8e52;
  }

  .allpost_image {
    width: 100%;
  }

  .allpost_image img {
    margin: 10px auto;
    width: 90%;
    height: 80vw;
  }
  .allpost_content {
    display: flex;
    flex-direction: column;
    width: 90%;
  }
  .allpost_content_author_section {
    display: flex;
    justify-content: space-between;
    font-size: 10px;
    opacity: 0.8;
  }

  .allpost_content_author_section button {
    border: none;
    font-weight: bold;
    padding: 2px;
  }

  .allpost_content_heading {
    font-size: 26px;
    font-weight: bold;
  }

  .allpost_content_body p {
    font-size: 15px;
    opacity: 0.8;
  }

  .allpost_content_body a {
    margin-left: 70%;
    opacity: 0.8;
  }
}
